	module.exports = {"data":{"markdownRemark":{"html":"<h2>Fitts Law</h2>\n<p>Fitts’s Law provides a model of human movement which can accurately predict the amount of time taken to move to and select a target. In human-computer interaction Fitts’s Law is typically applied to movement through the graphical user interface using a cursor or other type of pointer.</p>\n<p>The law describes a simple rule of UI design that the distance from one point to the next should be reduced, while having the target object be larger to reduce the amount of time for selecting a target. Ideally, the further the user has to traverse towards a target, the larger that target should be in order to make the interaction more smooth.</p>\n<img src=\"http://cdn.sixrevisions.com/0128-05_chart.jpg\" class=\"layzr-loaded\">\n<h4>More Information:</h4>\n<p>A very robust article about Fitts law: <a href=\"https://www.interaction-design.org/literature/article/fitts-s-law-the-importance-of-size-and-distance-in-ui-design\">https://www.interaction-design.org/literature/article/fitts-s-law-the-importance-of-size-and-distance-in-ui-design</a></p>","fields":{"slug":"/user-experience-design/fitts-law/"},"frontmatter":{"title":"Fitts Law"}}},"pathContext":{"slug":"/user-experience-design/fitts-law/"}}

